/*--------------------------------------------------------------
# Global
--------------------------------------------------------------*/

.wb-icons {
    font-size: 18px;
    min-width: 24px;
    height: 24px;
    line-height: 24px;
    display: inline-block;
}

.wb-icons:before {
    margin: 0;
}

#whats-new-options .post-elements-buttons-item {
    margin-right: 10px;
    margin-bottom: 10px;
    display: inline-block;
    position: relative;
    vertical-align: middle;
}

#whats-new-submit {
    clear: both;
}

#whats-new-toolbar .post-elements-buttons-item {
    margin-right: 10px;
    position: relative;
}

@media screen and (min-width: 46.8em) {
    #whats-new-options .post-elements-buttons-item:first-child {
        margin-left: 65px;
    }
}

.checkins-icon {
    cursor: pointer;
}

.bpchk-marker-container {
    margin: 0;
    display: inline-block;
    background: transparent;
}

.rtmedia-plupload-container .bpchk-marker-container {
    margin-left: 0;
}

.bp-checkins.bp-checkin-panel {
    width: calc(100% - 65px);
    margin: 10px 0;
    margin-left: 65px;
}

.bpchk-single-location-added {
    font-size: 14px;
    padding: 10px;
}

#bpchk-cancel-checkin .fa.fa-times {
    padding: 0px;
    background: black;
    border-radius: 100%;
    width: 18px;
    height: 18px;
    color: #fff;
    text-align: center;
    line-height: 17px;
    margin-left: 5px;
}

#bpchk-cancel-checkin .fa.fa-times:hover {
    background: red;
}

#bpchk-temp-location {
    font-size: 16px;
    text-decoration: underline;
}

.bpchk-place-map iframe {
    width: 100%;
}

button.bpchk-myplace-accordion {
    background-color: hsl(0, 0%, 87%);
    border: medium none;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    color: hsl(0, 0%, 27%);
    cursor: pointer;
    font-size: 15px;
    margin: 0 5%;
    outline: medium none;
    padding: 15px 20px;
    text-align: left;
    transition: all 0.4s ease 0s;
    width: 90%;
}

button.bpchk-myplace-accordion.active,
button.bpchk-myplace-accordion:hover {
    background-color: hsl(207, 90%, 54%);
    color: white;
}

div.panel {
    padding: 0 18px;
    background-color: white;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.2s ease-out;
    margin: 0px 5%;
}

button.bpchk-myplace-accordion::after {
    color: hsl(0, 0%, 47%);
    content: "+";
    float: right;
    font-size: 13px;
    font-weight: bold;
    margin-left: 5px;
}

button.bpchk-myplace-accordion::after:hover {
    color: white;
}

button.bpchk-myplace-accordion.active::after {
    content: "âˆ’";
    color: white;
}

.bpchk-myplace-admin-row {
    clear: both;
    overflow: hidden;
    padding-bottom: 5px;
}


